.maindiv{
    width: 100%;
    margin: 0;
}
.leftNav{
    padding-left: 10px;
    width: 25%;
    float: left;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.profile_image{
    width: 100%;
    height: 100%;
    display: inline-block;
    overflow: hidden;
    line-height: 1;
    vertical-align: middle;
    border-radius: 6px;
    margin-top: 10px;
}

.main_name{
    color: #24292e;
    font-weight: bold;
    font-size: 26px;
    line-height: 30px;
}
.main_id{
    font-size: 20px;
    font-style: normal;
    font-weight: 300;
    line-height: 24px;
    color: #666;
}

.buttonFollow{
    margin-top: 20px;
    width: 100%;
    height: 30px;
    /* font-weight: bolder; */

    color: #24292e;
    background-color: #eff3f6;
    background-image: linear-gradient(-180deg,#fafbfc,#eff3f6 90%);
    position: relative;
    display: inline-block;
    padding: 6px 12px;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    white-space: nowrap;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-repeat: repeat-x;
    background-position: -1px -1px;
    background-size: 110% 110%;
    border: 1px solid rgba(27,31,35,.2);
    border-radius: .25em;
    -webkit-appearance: none;
}

.bioData{
    width: 100%;
    margin-top: 20px;
    font-size: 14px;;
}

path{
    fill: gray;
}

.rightNav{
    float: right;
    width: 70%;
}
ul{
    padding-left: none;
    margin-left: none;
}

ul#menu li{
    display: inline;
    padding: 10px;
    overflow-x: scroll;
    display: inline;
    margin-left: 5px;
    margin-right: 30px;
    font-size: 14px;
    line-height: 1.5;
    color: #586069;
    text-align: center;
    border-bottom: 2px solid transparent;
    text-decoration: none;
    background-color: initial;
    box-sizing: border-box;
}

ul#menu li span{
    color: black;
    background-color: #ededee;
    border-radius: 10px;
    padding: 3px;
}
.menuItems{
    overflow-x: scroll;
    display: inline;
    padding: 8px;
    margin-left: 5px;
    margin-right: 30px;
    font-size: 14px;
    line-height: 1.5;
    color: #586069;
    text-align: center;
    border-bottom: 2px solid transparent;
    text-decoration: none;
    background-color: initial;
    box-sizing: border-box;
}

ul#menu li .active{
    font-weight: bolder;
    color: #000000;
    border-bottom: 2p solid black
}
.rightTopMenu{
    /* margin-left: 20px; */
    padding-top: 20px;
    width: 100%;
}

/* Smartphones (landscape) ----------- */
@media only screen
and (max-width : 728px) {
    .rightNav, .leftNav{
        float: left;
        width: 100%;
    }
    .rightTopMenu{
        width: 100%;
        overflow-x: scroll;
    }
    .profile_image{
        width: 60%;
        height: 60%;
    }
    .imagediv{
        width: 40%;
        float: left;
    }
    .nameSection{
        width: 60%;
        float: right;
    }
    .bioData{
        width: 100%;
    }
}

/* Smartphones (portrait) ----------- */
@media only screen
and (max-width : 320px) {
    .rightNav, .leftNav{
        float: left;
        width: 100%;
    }
    .rightTopMenu{
        width: 100%;
        overflow-x: scroll;
    }
}