  /*Left Panel*/
  .leftPanel{
    margin-top:3%;
    margin-left:13%;
    width:20%;
    display: inline-block;
  }
  .profile-pic{
    width:100%;
    height: 272px;
    border-radius:5px;
  }
  .profile-name{
    font-size:20px;
    color:gray;
  }
  .edit-btn{
    width:100%;
    padding:10px;
    border-radius:5px;
    border:1px solid gray;
    font-weight:bold;
  }

  /*Right Panel*/
  .rightPanel{
    margin-top:3%;
    margin-right:10%;
    width:55%;
    display: inline-block;
  }
  .tabs{
    border-bottom:1px solid lightgray;
    margin:10px;
  }
  .tabs li{
    display: inline-block;
    margin:10px;
  }
  .tabs li a{
    text-decoration: none;
    color:black;
    font-size:16px;
    padding:5px;
  }
  .tabs li a:hover{
    border-bottom:2px solid gray;
    transition:0.2s;
  }
  .active{
    border-bottom: 2px solid red;
  }
  .reposCount{
    margin:5px;
    padding:2px;
    background-color:lightgray;
    border-radius:10px;
  }

  /*Search Panel*/
  .search-input{
    width:70%;
    padding-left:8px;
    border:1px solid lightgray;
    border-radius:3px;
    font-size: 14px;
  }
  .search-input:focus{
    border:4px solid rgb(119, 116, 116,0.4);
  }
  .search-type,.search-language,.newbtn{
    padding:8px;
    border-radius:3px;
    background-color:rgb(238, 234, 234);
    font-weight: bold;
    margin:2px;
  }
  .searchResults{
    font-size:14px;
  }
  .searchPanel, .search-list, .searchResults{
    border-bottom:1px solid lightgray;
    margin:10px;
    padding:10px;
    color: rgb(119, 116, 116);
  }
  .search-list,.searchResults{
    list-style-type: none;
  }
  .search-list a{
    text-decoration: none;
    color:blue;
    font-weight: bold;
    font-size: 22px;;
  }
  .search-list a:hover{
    border-bottom:2px solid blue;
  }
  .search-list small{
    margin:5px;
  }
  .clearFilter{
    float:right;
    padding: 5px;
    margin-top: -10px;
    border-radius: 5px;
    background-color: white;
    border: none;
    font-size: 14px;
  }
.showBall{
    height: 10px;
  width: 10px;
  background-color: rgb(192, 10, 10);
  border-radius: 50%;
  display: inline-block;
}
small{
    color: rgb(68, 66, 66)
}
  /*Responsive*/
  @media screen and (max-width: 992px) {
      .leftPanel,.rightPanel {
        width:100%;
        margin:0%;
      }
      .leftPanel{
        margin-left:5%;
        margin-right:5%;
        width:90%;
        margin-top:5%;
      }
      .rightPanel{
        margin-top:10%;
      }
      .search-input{
        width:95%;
      }
  }
p{
  overflow-x: break;
}